.main {
  width: 100vw;
  height: 100vh;
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
}
.main1 {
  width: 40vw;
  height: 20vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 5vmax;
  color: purple;
  font-weight: bolder;
  font-variant: small-caps;
}
.main2 {
  width: 40vw;
  height: 20vh;
  border: 0.5vmax solid purple;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.main22 {
  width: 50%;
  height: 20vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.main3 {
  width: 40vw;
  height: 20vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.btn {
  width: 20vw;
  height: 10vh;
  background-color: purple;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 1.5vmax;
  cursor: pointer;
  font-weight: bolder;
  font-variant: small-caps;
}
.btn2 {
  width: 18vw;
  height: 10vh;
  background-color: purple;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 1.5vmax;
  cursor: pointer;
  font-weight: bolder;
  font-variant: small-caps;
}
.time {
  width: 26%;
  height: 90%;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3vmax;
}
.colon {
  width: 5%;
  height: 90%;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3vmax;
}
